.header {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.header__nav {
  position: relative;
  background-image: url("../../public/img/header/nav-menu-bg.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  max-width: 660px;
  max-height: 84px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  margin: 48px auto 0;

  &::before,
  &::after {
    content: "";
    position: absolute;
    height: 54px;
    width: 51px;
    background: url("../../public/img/header/nav-arrow.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }

  &::before {
    left: 0;
    top: 16.5px;
  }

  &::after {
    transform: scale(-1, -1);
    right: 3px;
    top: 14px;
  }
}

.header__nav-links {
  max-width: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.header__nav-link-text {
  position: relative;
  background: linear-gradient(180deg, #216933 0%, #e9ec42 100%), #ffffff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  &::after {
    content: "";
    position: absolute;
    background-color: #ffffffd6;
    max-width: 60%;
    width: 0;
    height: 2px;
    border-radius: 2px;
    right: 0;
    bottom: -3px;
  }

  &:hover {
    color: #ffffff;
    -webkit-text-fill-color: #ffffffd6;
   
    &::after {
      width: 100%;
      transition: .6s all;
    }
  }
}

.header__nav-link-text--active {
  color: #ffffff;
  -webkit-text-fill-color: #ffffffd6;

  &::after {
    content: "";
    position: absolute;
    background-color: #ffffffd6;
    max-width: 60%;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    right: 0;
    bottom: -3px;
  }
}

.header__nav-link-img {
  margin-bottom: -4px;
}

.header__nav-logo {
  width: 63px;
  height: 45px;
  object-fit: contain;
  margin-top: -5px;
}

.header__bg-video {
  position: absolute;
  z-index: -1;
  top: -5%;
  display: block;
  width: 100%;
}
